import React from 'react'
import './navbar.css'
import SearchIcon from '@mui/icons-material/Search';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import Avatar from '@mui/material/Avatar';
import Badge from '@mui/material/Badge';
import { Link } from 'react-router-dom';
const Navbar = () => {
  return (
    <>
    <header>
        <nav>
            <div className='left'>
                <div className='navlogo'>
                    <Link to="/">
                    <img src='https://t4.ftcdn.net/jpg/04/09/33/37/360_F_409333748_t7ZXrY2CuwQR8Vmibb2ILmBcOTMiInJ1.jpg'className='img-fluid'alt='' style={{width:"3rem",height:"2rem"}}/>
                    </Link>
                </div>
                <div className='nav_searchbaar'>
                    <input type="text"name=""id='' placeholder='Search'/>
                    <div className='search_icon'>
                        <SearchIcon id="search"/>
                    </div>
                </div>
            </div>
            <div className='right' style={{overflow:"hidden",textDecoration:"none"}}>
            <div className='nav_btn'>
                <Link to='/login'>Signin</Link>
            </div>
            <div className='cart_btn'>
                <Badge badgeContent={0} color="primary">
                    <ShoppingCartIcon id="icon"/>
                </Badge>
                <p>Cart</p>
            </div>

            <div>
            <Avatar className='avtar'/>
            </div>
            </div>
        </nav>
    </header>
    
    
    
    
    </>
  )
}

export default Navbar