import React ,{useState}from 'react'
import Footer from '../Footer/Footer'
import './cardpay.css'
const CardPay = () => {

const [btn,setBtn] =useState(0)

const btns =()=>{
    setBtn(btn + 1)
}
const button =()=>{
    if (btn == 0) return;
    setBtn(btn - 1);
}

  return (
    <>
    <div className='container my-5' style={{marginTop:"97px"}}>
       
            <div className='d-flex flex-row align-items-start mt-5'>
                <div className='col-8 d-flex flex-column m-2'>
                        <div className='cart-item p-3'>
                            <div className='d-flex flex-row '>
                                <img className="col-2 img-fluid" src='https://5.imimg.com/data5/ANDROID/Default/2021/6/TC/SN/CD/131258148/81tc5wowewl-sl1500-jpg-500x500.jpg'  alt=''/>
                                <div className='col-6 p-2'>
                                    <h5>Product Name</h5>
                                    <h6>Brand</h6>
                                    <p>Price</p>
                                </div>
                                <div className='col-2 p-2'>
                                <button className='btn btn-outline-primary ' onClick={button}> -</button>
                                {btn}
                                <button className='btn btn-outline-primary 'onClick={btns}>+</button>
                                </div>
                                <div className='col-2 d-flex justify-content-end close'>
                                    <i className='bi bi-x-circle'></i>
                                </div>
                            </div>

                        </div>
                        <div className='cart-item p-3'>
                            <div className='d-flex flex-row '>
                                <img className="col-2 img-fluid" src='https://5.imimg.com/data5/ANDROID/Default/2021/6/TC/SN/CD/131258148/81tc5wowewl-sl1500-jpg-500x500.jpg'  alt=''/>
                                <div className='col-6 p-2'>
                                    <h5>Product Name</h5>
                                    <h6>Brand</h6>
                                    <p>Price</p>
                                </div>
                                <div className='col-2 p-2'>
                                <button className='btn btn-outline-primary ' onClick={button}> -</button>
                                {btn}
                                <button className='btn btn-outline-primary 'onClick={btns}>+</button>
                                </div>
                                <div className='col-2 d-flex justify-content-end close'>
                                    <i className='bi bi-x-circle'></i>
                                </div>
                            </div>

                        </div>
                        <div className='cart-item p-3'>
                            <div className='d-flex flex-row '>
                                <img className="col-2 img-fluid" src='https://5.imimg.com/data5/ANDROID/Default/2021/6/TC/SN/CD/131258148/81tc5wowewl-sl1500-jpg-500x500.jpg'  alt=''/>
                                <div className='col-6 p-2'>
                                    <h5>Product Name</h5>
                                    <h6>Brand</h6>
                                    <p>Price</p>
                                </div>
                                <div className='col-2 p-2'>
                                <button className='btn btn-outline-primary ' onClick={button}> -</button>
                                {btn}
                                <button className='btn btn-outline-primary 'onClick={btns}>+</button>
                                </div>
                                <div className='col-2 d-flex justify-content-end close'>
                                    <i className='bi bi-x-circle'></i>
                                </div>
                            </div>

                        </div>
                
                </div>
                    <div className='col-4 order p-3 m-2'>
                        <h4>Order Total</h4>
                        <div className='d-flex-flex-row py-3'>
                            <input type='text'className="form-control" placeholder='Promo code'/>
                            <button className='btn btn-primary'>Apply</button>
                        </div>
                        <div className='d-flex flex-row justify-content-between p-2 '>
                            <span className='billing-item text-muted'>items</span>
                            <span className='billing-cost'>$100</span>
                        </div>
                        <div className='d-flex flex-row justify-content-between p-2 '>
                            <span className='billing-item text-muted'>Shopping</span>
                            <span className='billing-cost'>$10</span>
                        </div>
                        <div className='d-flex flex-row justify-content-between p-2 '>
                            <span className='billing-item text-muted'>Discount</span>
                            <span className='billing-cost'>-$00</span>
                        </div>
                        <div className='d-flex flex-row justify-content-between p-2 '>
                            <span className='billing-item text-muted fs-5'>Total</span>
                            <span className='billing-cost fs-5'>-$100</span>
                        </div>

                <div className='d-flex mt-3'>
                    <button className='btn btn-outline-success flex-grow-1 '>Pay Now</button>
                </div>
                    </div>
            </div>
    </div>
    
    
    
    
    
    
    
    
    <Footer/>
    
    
    </>
  )
}

export default CardPay