import React from 'react'
import Carousel from 'react-material-ui-carousel';
import './banner.css';

const data =[
  "https://www.raypcb.com/wp-content/uploads/2021/03/c81d71c0734dd19.jpg",
  "https://image.shutterstock.com/image-vector/appliances-set-colored-icons-collection-260nw-282445358.jpg",
  "https://www.efordern.com/uploads/a-rpluna8spamnrbi5ne763ah4ihrj9pa10d2ut_11-01-16_03-08-12.jpg",
  "https://www.efordern.com/uploads/a-rpluna8spamnrbi5ne763ah4ihrj9pa10d2ut_11-01-16_03-08-12.jpg",
  "https://www.serviceslk.com/wp-content/uploads/2020/04/Electrical-Items-1.png",
  "https://haniffa.com.sg/wp-content/uploads/2021/01/Main_Banner_ElectricalHomeApp-1024x397.jpg",
  "https://karshika.com/storage/product_images/1650591889_wires-and-cables-iritty-kannur-kerala.jpg",
  "https://image.shutterstock.com/image-photo/electrical-equipment-background-texture-260nw-355437692.jpg"
]



const Banner = () => {
  return (
    <>
    
    {/* first slider in ===react-carousel */}
    <Carousel
     className='carasousel'
     autoPlay={true}
     animation='slide'
     indicators={false}
     navButtonsAlwaysVisible={true}
     cycleNavigation={true}
     navButtonsProps={{
      style:{
        backgroundColor:"#fff",
        color:"#494949",
        borderRadius:0,
        marginTop:-22,
        height:"104px"
      }}}
     >
    {data.map((img,i)=>{
      return(
        <>
        <img src={img}alt=''key={img. id} className='banner_img' />
        
        
        
        </>
      )
    })}
    </Carousel>
    
    {/* first slider in ===react-carousel    end */}
    
    
    
    
    
    
    
    
    
    </>
  )
}

export default Banner