import React, { useState } from 'react'
import { Link } from 'react-router-dom';
import './login.css';




const SignUp = () => {

const [udata, setUdata] =useState({
    your_name:"",
    email:"",
    number:"",
    password:"",
    cpassword: "",
});
console.log(udata)
const adddata=(e)=>{
    const{name,value}= e.target;
    setUdata(()=>{
        return{
            ...udata,
            [name]:value
        }
    })
}






  return (
   <>
     <section>
        <div className="sign_container">
          <div className="sign_header">
              <h4 className="text-center mt-4">D Nexus</h4>
            </div>
            <div className="sign_form mt-3">
              <form>
                <h2 className='text-center'>Sign-Up</h2>
                <div className="form_data">
                  <label htmlFor="email">Your Name</label>
                  <input type="text"
                  onChange={adddata}
                  value={udata.your_name}
                   name="your_name" id="name" />
                </div>
                <div className="form_data">
                  <label htmlFor="password">Email</label>
                  <input type="text"
                 onChange={adddata}
                 value={udata.email}
                   name="email" id="email"  />
                </div>
                <div className="form_data">
                  <label htmlFor="number">Mobile</label>
                  <input type="text"
                  onChange={adddata}
                  value={udata.number}
                   name="number" id="number" />
                </div>
                <div className="form_data">
                  <label htmlFor="password">Password</label>
                  <input type="password"
                  onChange={adddata}
                  value={udata.password}
                   name="password" id="password" placeholder="At least 6 char" />
                </div>
                <div className="form_data">
                  <label htmlFor="password">Comfirm Password</label>
                  <input type="password"
                  onChange={adddata}
                  value={udata.cpassword}
                   name="cpassword" id="cpassword" placeholder="At least 6 char" />
                </div>
                <button className="signin_btn" type="submit">SignUp</button>
                <div className='signin_info'>
                <p>Already have an Acount?<Link to="/login">Signin</Link></p>
                </div>
              </form>
            </div>
            <div className="create_accountinfo ">
              <p>New To  D-Nexus</p>
                <Link to="/login">
                    <button>Login </button>
                    </Link>

            </div>

          </div>
      
      </section>
   
   
   </>
  )
}

export default SignUp