import React, { useState } from "react";
import { Link } from "react-router-dom";
import "./login.css";

const Login = () => {
  const [logdata, setData] = useState({
    email: "",
    password: "",
  });
  console.log(logdata);

  const adddata = (e) => {
    const { name, value } = e.target;

    setData(() => {
      return {
        ...logdata,
        [name]: value,
      };
    });
  };

  return (
    <>
      <section>
        <div className="sign_container">
          <div className="sign_header">
            <h4 className="text-center mt-4">D Nexus</h4>
          </div>
          <div className="sign_form mt-3">
            <form>
              <h2>Sign-In</h2>
              <div className="form_data">
                <label htmlFor="email">Email</label>
                <input
                  type="text"
                  onChange={adddata}
                  value={logdata.email}
                  name="email"
                  id="email"
                />
              </div>
              <div className="form_data">
                <label htmlFor="password">Password</label>
                <input
                  type="password"
                  onChange={adddata}
                  value={logdata.password}
                  name="password"
                  id="password"
                />
              </div>
              <button className="signin_btn" type="submit">
                Login
              </button>
            </form>
          </div>
          <div className="create_accountinfo mt-3">
            <p className="text-center">New To D-Nexus </p>
            <Link to="/signup">
              <button>Create Your D-Nexus Acount</button>
            </Link>
          </div>
        </div>
      </section>
    </>
  );
};

export default Login;
