import { Divider } from "@mui/material";
import {Link} from "react-router-dom";
import React from "react";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import  {products } from './Productdata';
import './slider.css'
//  carousel multi=====

const responsive = {
  desktop: {
    breakpoint: { max: 3000, min: 1024 },
    items: 3,
  },
  tablet: {
    breakpoint: { max: 1024, min: 464 },
    items: 2,
  },
  mobile: {
    breakpoint: { max: 464, min: 0 },
    items: 1,
  },
};

const Slider = ({title}) => {
  return (
    <>
      <div className="products_section">
        <div className="products_deal">
          <h2>{title}</h2>
          <div className="text-end mb-1">
              <button className="view_btn ">View All</button>
          </div>
        </div>
        <Divider />
        <Carousel
                responsive={responsive}
                infinite={true}
                draggable={false}
                swipeable={true}
                centerMode={true}
                autoPlay={true}
                autoPlaySpeed={4000}
                keyBoardControl={true}
                showDots={false}
                removeArrowOnDeviceType={["tablet", "mobile"]}
                dotListClass="custom-dot-list-style"
                itemClass="carousel-item-padding-40-px"
                containerClass="carousel-container"
            >


      

         {
          products.map((e)=>{
            return(
              <>
              <div className="container">
                 <div class=" card-body mb-2">
                  <img src={e.url} class="card-img-top" alt="..." style={{height:"200px",width:"200px"}}/>
                  <div class="card-body">
                    <h5 class="card-title"style={{ color: "#007185" }}>{e.title.shortTitle}</h5>
                    <p>Price ₹ {e.price.cost}</p>
                    <p>{e.discount}</p>
                    <p>{e.tagline} <span className='float-end rating-star'>
                      
                    <i class="bi bi-star-fill "></i>
                    <i class="bi bi-star-fill "></i> 
                    <i class="bi bi-star-fill "></i>
                      
                      </span></p>
                   
                    <Link to="/carddetails" className="text-center">
                    <button  class="btn btn-primary">ADD TO CARD</button>
                    </Link>
                  </div>
                </div> 
              </div>

        {/* <div className='col-xl-3 col-lg-4 col-md-6 mb-2'>
            <div class="card product-item" >
                    <img src="https://5.imimg.com/data5/ANDROID/Default/2021/6/TC/SN/CD/131258148/81tc5wowewl-sl1500-jpg-500x500.jpg" class="card-img-top" alt="..." style={{height:"200px",width:"200px"}}/>
                    <div class="card-body">
                        <h5 class="card-subtitle mb-2 text-muted">Camera</h5>
                        <h5 class="card-title">Sony WX-5</h5>
                      <p>$100.76<span className='float-end rating-star'>
                      <i class="bi bi-star-fill "></i>
                      <i class="bi bi-star-fill "></i>
                      <i class="bi bi-star-fill "></i>
                        </span></p>
                        <div className='text-center'>
                        <a href="#" class="btn btn-outline-primary">Add To Card</a>
                        </div>
                    </div>
                </div>
           </div> */}









             
              {/* <div className="product_items">
                <div className="product_img">
                  <img src={e.url}alt="productitem"/>
                </div>
                <p className="products_name">{e.title.shortTitle}</p>
                <p className="products_offer"style={{ color: "#  007185" }}>{e.discount}</p>
                <p className="products_explore">
                  {e.tagline}
                </p>
                <div className="float-end ">

                <i class="bi bi-star-fill"></i>
                <i class="bi bi-star-fill"></i>
                <i class="bi bi-star-fill"></i>
                </div>

              </div> */}
              
              
              
              </>
            )
          })
         }
        </Carousel>
        ;
        
      </div>
    </>
  );
};

export default Slider;
